<template>
  <van-popup class="sku-popup" v-model="visible" position="right">
    <div class="dt">应用类型</div>
    <ul class="dd c-list">
      <li
        class="c-item"
        v-for="(item, index) in sku1List"
        :key="index"
        :class="{'is-active': item.name === sku1Val}"
        @click="sku1Val=item.name"
      >
        <span>{{ item.name }}</span>
      </li>
    </ul>
    <div class="dt">项目类型</div>
    <ul class="dd c-list">
      <li
        class="c-item"
        v-for="(item, index) in sku2List"
        :key="index"
        :class="{'is-active': item.name === sku2Val}"
        @click="sku2Val=item.name"
      >
        <span>{{ item.name }}</span>
      </li>
    </ul>
    <div class="btns-wrapper">
      <van-button class="btn" type="primary" size="small" @click="handleReset">重置</van-button>
      <van-button class="btn" type="primary" size="small" @click="handleConfirm">确认</van-button>
    </div>
  </van-popup>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      sku1Val: '不限',
      sku2Val: '不限',
      visible: this.value,
      sku1List: [
        {
          name: '不限'
        },
        {
          name: '移动应用'
        },
        {
          name: 'H5应用'
        },
        {
          name: '微信应用'
        },
        {
          name: '小程序应用'
        },
        {
          name: 'WEB应用'
        }
      ],
      sku2List: [
        {
          name: '不限'
        },
        {
          name: '项目'
        },
        {
          name: '悬赏'
        }
      ]
    };
  },
  watch: {
    value(val) {
      this.visible = val;
    },
    visible(val) {
      this.$emit('input', val);
    }
  },
  methods: {
    handleReset() {
      this.sku1Val = this.sku1List[0]['name'];
      this.sku2Val = this.sku2List[0]['name'];
    },
    handleConfirm() {
      this.$emit('confirm', {
        appType: this.sku1Val,
        projectType: this.sku2Val
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.c-list {
  overflow: hidden;
  padding-right: 0;
  background: #fff;
}

.c-item {
  display: inline-block;
  width: 40%;
  line-height: 24px;
  padding: 4px 4px;
  font-size: 12px;
  border-radius: 3px;
  margin-bottom: 10px;
  margin-right: 6px;
  text-align: center;
  color: #666;
  border: 1px solid #eceaea;
  &.is-active {
    color: #fff;
    background: #009688;
  }
}

.btns-wrapper {
  margin-top: 20px;
  .btn {
    margin-right: 10px;
  }
}

.sku-popup {
  width: 60%;
  height: 100%;
  padding-left: 12px;
  .dt {
    font-size: 16px;
    line-height: 24px;
    margin-top: 10px;
  }
  .dd {
    margin-top: 10px;
  }
}
</style>



